<template>
  <div class="relative">
    <!-- <Button class="mt-3" color="teal" view="smooth" dense @click="resetZoom">Reset Zoom</Button> -->
    <canvas ref="chartRef" height="80" id="charts"></canvas>
  </div>
</template>

<script>
import Chart from 'chart.js/auto'
import zoomPlugin from 'chartjs-plugin-zoom'
import { onMounted, ref, watch } from 'vue'
import { useUser } from 'composable/user'

import Button from 'bbUi/components/button/button.vue'

import { money } from 'utils/number'

export default {
  components: {
    Button
  },
  props: {
    labels: {
      type: Array,
      default: () => []
    },
    datasets: {
      type: Array,
      default: () => []
    },
    type: {
      default: 'line',
      type: String
    }
  },
  setup(props) {
    const chartRef = ref(null)
    const { currencySymbol } = useUser()

    const chartState = {
      chart: null,
      props: { ...props }
    }

    const render = (labels = [], datasets = []) => {
      if (chartState.chart) {
        return chartState.chart.update('reset')
      }

      var gradient = document
        .getElementById('charts')
        .getContext('2d')
        .createLinearGradient(0, 0, 0, 400)
      gradient.addColorStop(0, '#4ED3BB')
      gradient.addColorStop(0.5, '#4ED3BB')
      gradient.addColorStop(1, '#FFFFFF')

      Chart.register(zoomPlugin)
      Chart.defaults.font.family = "'Poppins'"
      return (chartState.chart = new Chart(chartRef.value.getContext('2d'), {
        type: chartState.props.type,
        responsive: false,
        maintainAspectRatio: false,
        options: {
          elements: {
            point: {
              radius: 4
            }
          },
          plugins: {
            legend: {
              display: true
            },
            tooltip: {
              enabled: true,
              position: 'nearest',
              mode: 'index',
							filter: (data) => {
								data.label = data.raw.label
								data.formattedValue = `$${money(data.formattedValue)}`
								return data
							}
            }
          },
          scales: {
            y: {
              beginAtZero: true,
              ticks: {
                callback: (value) => {
                  return `$${money(value)}`
                },
              }
            }
          },
					parsing: {
						xAxisKey: 'x',
						yAxisKey: 'y'
					}
        },
        data: {
          labels: labels,
          datasets: datasets
        }
      }))
    }

    const destroy = () => {
      if (chartState.chart) {
        chartState.chart.destroy()
        chartState.chart = null
      }
    }

    watch(
      () => ({
        data: props.datasets
      }),
      () => {
        destroy()
        render(props.labels, props.datasets)
      },
      {
        deep: true
      }
    )

    onMounted(() => render(props.labels, props.datasets))

    return {
      chartRef
    }
  }
}
</script>