<template>
  <div>
    <Breadcrumb
      page-title="Usage"
      :items="breadcrumb"
      :back-to="{ name: 'setting' }"
    />
    <div class="grid grid-cols-2 gap-8 mt-14 mb-10">
      <Card>
        <template #default>
          <div class="p-4">
            <p class="mb-2">Next Invoice Date</p>
            <h6 class="text-3xl">{{ nextInvoiceDate }}</h6>
          </div>
        </template>
      </Card>
      <Card>
        <template #default>
          <div class="p-4">
            <p class="mb-2">Estimated Invoice Total</p>
            <h6 class="text-3xl my-2">
              <template v-if="estimatedInvoiceTotal !== '-'">
                ${{estimatedInvoiceTotal}}
              </template>
              <template v-else>
                {{ estimatedInvoiceTotal }}
              </template>
            </h6>
            <a class="select-none" @click.prevent="handleOpenModal"
              >See cost breakdown</a
            >
          </div>
        </template>
      </Card>
    </div>
    <div class="grid grid-cols-1 gap-8">
      <Card>
        <template #default>
          <div class="flex justify-between items-center">
            <div>
              <p class="my-3">
                Total Buybacks:
                <a @click.prevent>
                  {{ chartSummary.total_buybacks }}
                </a>
              </p>
              <p class="my-3">
                Total Leads:
                <a @click.prevent>
                  {{ chartSummary.total_leads }}
                </a>
              </p>
              <p class="my-3">
                Total Amount of Leads:
                <a @click.prevent>
                  <Currency :value="chartSummary.total_amount_leads"></Currency
                ></a>
              </p>
            </div>
            <div>
              <GroupContainer>
                <Select
                  :loading="loading.usage.chart"
                  :modelValue="state.usage.chart.selected_date"
                  @change="handleSelectDateOption"
                  :items="dateOptions"
                  placeholder="Select Date"
                  name="name"
                  by="name"
                ></Select>
                <Select
                  :loading="loading.usage.chart"
                  :modelValue="state.usage.chart.selected_year"
                  @change="handleSelectYear"
                  :items="yearOptions"
                  name="year"
                  by="year"
                  placeholder="Select Year"
                ></Select>
              </GroupContainer>
            </div>
          </div>
          <Chart
            v-loading="loading.usage.chart"
            :datasets="chartDataset"
            :labels="chartLabels"
          />
        </template>
      </Card>
    </div>
    <Modal v-model="modal.usage" title="Cost Breakdown" size="xs">
      <template #default>
        <div class="flex justify-between">
          <div class="pr-4">
            <p class="block mb-2">
              {{ state.usage.total_lead }} Total buyback leads
            </p>
            <p class="block text-grey-04">
              0.5% x ${{state.usage.total}}
              (Total Amount of Buyback Leads)
            </p>
          </div>
          <div>
            ${{money(state.usage.total_invoice)}}
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex flex-wrap justify-between">
          <span class="text-xl font-bold">Total</span>
          <span class="text-xl font-bold">
            ${{money(state.usage.total_invoice)}}
          </span>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { computed, defineComponent, ref, watch } from 'vue'
import { state, loading, modal, usePlanUsage } from 'composable/setting/plan'

import { money } from 'utils/number'
import debounce from 'utils/debounce'
import moment from 'moment'

import Card from 'bbUi/components/card/card.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Select from 'bbUi/components/select/select.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Currency from 'components/Currency.vue'
import Chart from 'components/Plan/ChartPlanUsage.vue'

export default defineComponent({
  components: {
    Card,
    Modal,
    GroupContainer,
    Select,
    Breadcrumb,
    Currency,
    Chart
  },
  async setup() {
    const { actionShow, actionShowChart } = usePlanUsage()

    const breadcrumb = ref([
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Usage'
      }
    ])

    const nextInvoiceDate = computed(() =>
      state.usage.next_invoice !== null
        ? moment(state.usage.next_invoice, 'YYYY-MM-DD HH:mm:ss').format(
            'MMM DD, YYYY'
          )
        : '-'
    )
    const estimatedInvoiceTotal = computed(() =>
      state.usage.total_invoice ? money(state.usage.total_invoice) : '0'
    )
    const chartSummary = computed(() => ({
      total_buybacks: state.usage.chart.total_buyback
        ? state.usage.chart.total_buyback
        : 0,
      total_leads: state.usage.chart.total_lead
        ? state.usage.chart.total_lead
        : 0,
      total_amount_leads: state.usage.chart.total_lead_amount
        ? money(state.usage.chart.total_lead_amount)
        : money(0)
    }))
    const chartDataset = computed(() => {
      return [
        {
          label: 'Bill',
          color: '#1672ED',
          borderColor: '#1672ED',
          backgroundColor: '#1672ED',
          smooth: true,
          tension: 0.4,
          data: state.usage.chart.list_day.map((day, index) => {
            const xValue = parseInt(moment(day, 'YYYY-MM-DD').format('DD'))
            const label = moment(day, 'YYYY-MM-DD').format('MMM DD YYYY') || '-'
            return {
              x: !isNaN(xValue) ? xValue : 0,
              y: state.usage.chart.data_chart[index],
              label: label
            }
          })
        }
      ]
    })
    const chartLabels = computed(() =>
      state.usage.chart.list_day.map((day) =>
        moment(day, 'YYYY-MM-DD').format('MMM DD')
      )
    )
    const dateOptions = computed(() => state.usage.chart.list_date)
    const yearOptions = computed(() => state.usage.chart.list_year)

    const handleOpenModal = () => (modal.usage = true)
    const handleSelectDateOption = (datePeriod) => {
      if (!datePeriod) return
      const selectedDate = dateOptions.value.find(
        (date) => date.name === datePeriod
      )
      if (selectedDate) {
        Object.assign(state.usage.data.chart, {
          ...state.usage.data.chart,
          period_start: selectedDate.period_start,
          period_end: selectedDate.period_end
        })
      }
    }
    const handleSelectYear = (year) => {
      Object.assign(state.usage.data.chart, {
        ...state.usage.data.chart,
        period_start: null,
        period_end: null,
        year: year
      })
    }

    watch(
      () => state.usage.data.chart,
      debounce(async () => {
        await Promise.all([actionShow(), actionShowChart()])
      }, 250),
      {
        deep: true
      }
    )

    await Promise.all([actionShow(), actionShowChart()])

    return {
      state,
      loading,
      modal,
      breadcrumb,
      nextInvoiceDate,
      estimatedInvoiceTotal,
      chartDataset,
      chartLabels,
      chartSummary,
      dateOptions,
      yearOptions,
      money,
      handleSelectDateOption,
      handleSelectYear,
      handleOpenModal
    }
  }
})
</script>

