import { reactive } from 'vue'
import { isFunction, isObject } from 'utils/is'
import store from 'store'

const state = reactive({
  usage: {
    next_invoice: null,
    total: 0,
    total_buyback: 0,
    total_invoice: 0,
    total_lead: 0,
    chart: {
      selected_date: null,
      selected_year: null,
      data_chart: [],
      list_day: [],
      list_date: [],
      list_year: [],
      total_amount: 0,
      total_buyback: 0,
      total_lead: 0,
      total_lead_amount: 0
    },
    data: {
      chart: {
        year: null,
        period_start: null,
        period_end: null
      }
    }
  }
})
const loading = reactive({
  usage: {
    show: false,
    chart: false
  }
})
const modal = reactive({
  usage: false
})

const usePlanUsage = () => {
  const transformKey = new Map()
  transformKey.set('month', 'list_date')

  const actionShow = async (callback = () => {}) => {
    try {
      loading.usage.show = true
      const response = await store.dispatch('setting/plan/showUsage')
      if (isObject(response)) {
        for (let [key, value] of Object.entries(response)) {
          if (Object.keys(state.usage).includes(key)) state.usage[key] = value
        }
      }
      isFunction(callback) && callback()
    } finally {
      loading.usage.show = false
    }
  }
  const actionShowChart = async (callback = () => {}) => {
    try {
      loading.usage.chart = true
      const response = await store.dispatch('setting/plan/showUsageChart', state.usage.data.chart)
      if (isObject(response)) {
        for (let [key, value] of Object.entries(response)) {
          if (Object.keys(state.usage.chart).includes(key))
            state.usage.chart[key] = value
          if (transformKey.get(key) !== undefined)
            state.usage.chart[transformKey.get(key)] = value
        }

        if(response.month){
          const selectedChartPeriod = response.month.find(date => date.selected === 1)
            if(selectedChartPeriod) state.usage.chart.selected_date = selectedChartPeriod.name
        }
        if(response.list_year){
          const selectedChartYear = response.list_year.find(year => year.selected === 1)
          if(selectedChartYear) state.usage.chart.selected_year = selectedChartYear.year
        }
      }
      isFunction(callback) && callback()
    } finally {
      loading.usage.chart = false
    }
  }
  return {
    actionShow,
    actionShowChart
  }
}

export { state, loading, modal, usePlanUsage }
